.feed
{
    flex:0.5;
    border-right: 1px solid var(--twitter-background);
    min-width: fit-content;
    overflow-y: scroll;
   

}
.feed::-webkit-scrollbar{
    display: none;
}
.feed{
    -ms-overflow-style:none;
    scrollbar-width: none;
}
.feed_header
{
    position: sticky;
    top:0;
    background-color: white;
    z-index: 100;
    border:1px solid var(--twitter-background);
    padding:15px 20px;
}
.feed_header>h2 {
    font-size: 20px;
    font-weight: 800;
    
}